import React, {Component} from "react";
import TableRow from "./TableRow";
import MainGroup from "./MainGroup";
import $ from "jquery";

export default class EmailButton extends Component {
  state = {
    loading: "",
    delegateEmail: this.props.defaultDelegateEmail,
  };

  static isEmail(email) {
    const regex = /^[A-Za-z0-9._%+-]+@[A-Za-z0-9.-]+\.[A-Za-z]{2,}$/;
    if (email.match(/[,;]/)) {
      const emails = email.split(/\s*[,;]\s*/);
      for (let i=0; i < emails.length; i++) {
        const candidate = emails[i];
        if (!candidate.match(regex)) {
          return false;
        }
      }
      return true;
    } else {
      return email.match(regex);
    }
  }

  makeDelegateEmailIdentifier(name) {
    return TableRow.makeHTMLId(name)+"_delegateEmail";
  }

  checkShowDelegateButton() {
    const name = this.props.name;
    const delegateEmailIdentifier = this.makeDelegateEmailIdentifier(name);
    const delegateButtonIdentifier = delegateEmailIdentifier+"_button";
    const requestedEmail = $('#'+delegateEmailIdentifier).val();
    if (EmailButton.isEmail(requestedEmail)) {
      $('#'+delegateButtonIdentifier).show();
    } else {
      $('#'+delegateButtonIdentifier).hide();
    }
  }

  static makeMailto(email) {
    if (!email) {
      return "";
    }
    const smallEmail = 'smallEmail';
    if (email.match(/[;,]/)) {
      const emails = email.split(/[;,]/);
      const filteredEmails = [];
      for (let i=0; i < emails.length; i++) {
        if (EmailButton.isEmail(emails[i])) {
          filteredEmails.push(emails[i]);
        }
      }
      const hrefClause = 'mailto:'+filteredEmails.join(';');
      const display = filteredEmails.join('; ');
      return <a href={hrefClause} className={smallEmail}>{display}</a>;
    } else if (EmailButton.isEmail(email)) {
      const hrefClause = 'mailto:'+email;
      return <a href={hrefClause} className={smallEmail}>{email}</a>;
    }
    return "";
  }

  emailFaculty() {
    this.props.showModalForEmail(this.props.email, this.props.name, this.props.email);
  }

  emailDelegate() {
    const name = 'Delegate for '+this.props.name;
    const delegateEmailIdentifier = this.makeDelegateEmailIdentifier(this.props.name);
    const email = $('#'+delegateEmailIdentifier).val();
    this.props.showModalForEmail(email, name, this.props.email);
  }

  saveDelegateEmail() {
    const link = this.props.link ?? "";
    if (!link) {
      return;
    }
    const delegateEmailIdentifier = this.makeDelegateEmailIdentifier(this.props.name);
    const newValue = $('#'+delegateEmailIdentifier).val();
    if (newValue === this.state.delegateEmail) {
      return;
    }
    const query = {
      action: 'saveDelegateEmail',
      email: this.props.email,
      value: newValue
    };
    const request = MainGroup.makePostRequest(query);

    console.log(JSON.stringify(query));
    return new Promise((resolve, reject) => {
      fetch(link, request)
      .then(response => MainGroup.processResponse(response))
      .then(items => {
        console.log(JSON.stringify(items));
        if (items.error) {
          MainGroup.alertForError(items.error);
          reject();
        } else {
          this.setState({
            ...this.state,
            delegateEmail: newValue
          });
          resolve(true);
        }
      })
      .catch(error => MainGroup.alertForError(error));
    });
  }

  render() {
    const name = this.props.name;
    const paragraphKey = name+"_paragraph";
    const loadingKey = name+"_loading";
    const buttonKey = name+"_button";
    const delegateParagraphKey = name+"_delegateParagraph";
    const delegateKey = name+"_delegate";
    const delegateButtonKey = name+"_delegateButton";
    const delegateEmailIdentifier = this.makeDelegateEmailIdentifier(name);
    const delegateButtonIdentifier = delegateEmailIdentifier+"_button";
    const paragraphClasses = 'nomargin smaller';
    const hideStyle = this.state.delegateEmail ? {} : {display: 'none'};
    const delegateEmail = <p key={delegateParagraphKey} className={paragraphClasses}>
      Delegate Email: <input key={delegateKey} defaultValue={this.state.delegateEmail} id={delegateEmailIdentifier} onChange={this.checkShowDelegateButton.bind(this)} onBlur={this.saveDelegateEmail.bind(this)} /><br/>
      <button style={hideStyle} id={delegateButtonIdentifier} key={delegateButtonKey} onClick={this.emailDelegate.bind(this)}>Send Email to Delegate</button>
    </p>;

    let loadingInfo = "";
    if (this.state.loading) {
      loadingInfo = <p key={loadingKey} className={paragraphClasses}>{this.state.loading}</p>;
    }
    if (this.props.email) {
      return (
        <section>
          {delegateEmail}
          <p key={paragraphKey} className={paragraphClasses}>
            <button key={buttonKey} onClick={this.emailFaculty.bind(this)}>Send Email to Faculty</button>
          </p>
          {loadingInfo}
        </section>
      );
    } else {
      return (
        <section>
          {delegateEmail}
          <p key={paragraphKey} className={paragraphClasses}>
            (No faculty email provided.)
          </p>
          {loadingInfo}
        </section>
      );
    }
  }
}

